import { defineSupportCode, supportCodeLibraryBuilder } from 'cucumber';
import Bluebird from 'bluebird';

defineSupportCode(({ eventBroadcaster, setDefinitionFunctionWrapper }) => {
  // auto-enroll all step definitions. Other support code can enroll themselves inline.
  for (const definition of supportCodeLibraryBuilder.options.stepDefinitions)
    definition.options.wrapperOptions = { watchResponses: true };

  setDefinitionFunctionWrapper((fn, options) => {
    if (!options || !options.watchResponses)
      return fn;

    return function () {
      const args = arguments;

      return Bluebird.race([
        onResponseStatusCode(501, eventBroadcaster).return('pending'),
        Bluebird.try(() => fn.apply(this, args)),
      ]);
    };
  });
});

function onResponseStatusCode(statusCode, eventBroadcaster) {
  return new Bluebird(resolve => {
    function onResponse(data) {
      if (data.statusCode === statusCode)
        resolve();
    }

    eventBroadcaster.on('response', onResponse);

    eventBroadcaster.once('test-case-finished', () =>
      eventBroadcaster.removeListener('response', onResponse));
  });
}
